*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    position: relative;
    margin: 100px auto;
    text-align: center;
    min-width: 800px;
    max-width: 1200px;
    height: 70vh;
    border: 2px solid #673CC0;
    box-shadow: 2px 2px 10px #673CC0;
    border-radius: 10px;
}


.text-container {
    text-align: center;
}

.text-alert {
    font-size: 4rem;
    font-family: 'Shrikhand', cursive;
    line-height: 1.2;
    letter-spacing: 5px;
    color: #101C92;
    margin: 50px 0 20px 0;
}

.wish {
    font-size: 1.5rem;
    font-family: 'ZCOOL QingKe HuangYou', cursive;
    color: rgb(109, 7, 204);
}
.time, .date {
    position: absolute;
    bottom: 20px;
    font-size: 2.5rem;
    border: 2px solid #101C92;
    border-radius: 10px;
    background-color: #101C92;
    box-shadow: 4px 4px 10px rgba(16, 29, 146, 0.8);
    color: #fff;
    padding: 8px 16px;
    letter-spacing: 4px;
    font-family: 'ZCOOL QingKe HuangYou', cursive;
}

.time {
    left: 20px;
    width: 260px;
}
.date {
    right: 20px;
}
.quote-container{
    margin: 180px auto;
}

.quote-text, .quote-author {
    font-size: 1.2rem;
    background-color: rgb(109, 7, 204);
    display: inline-block;
    padding: 6px 12px;
    margin: 2px;
    color: #fff;
    border-radius: 8px;
    font-family: 'Montserrat', sans-serif;
}

.quote-text {
    font-weight: 300;
}

.quote-author {
    font-weight: 700;
}
